.center-container {
	display: flex;
	flex-direction: column;
	align-items: center; justify-content: center;
	height: 100vh;
}
.centered-input,
.centered-text {
	font-size: 1.8em; text-align: center;
	padding: 5px;
	border-radius: 17px;/*圓角*/
	border: 1px solid #CCC;/*細邊框,淡灰色*/
}
.error-message {
	color: red; text-align: center;
	margin-bottom: 1em;
	font-size: 1.5em;
}/*%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%% 以下是我自己新增 %%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%*/
.centered-text{/*計算結果*/
	margin: .5em auto -.5em;/*上 右左 下*/
	/**/
}
#err-anime{animation: err-anime .5s linear 1;}
@keyframes err-anime{
	0%{font-size: 1.5em;}
	50%{font-size: 1.8em;}
	100%{font-size: 1.5em;}
}
cc1{
	display: inline-block; position: relative; right: 1em;
	animation: rpTri1 1s linear infinite;
}
@keyframes rpTri1{
	100% {
		opacity: 0;
		transform: translate(100%, 0);
	}
}
cc2{
	display: inline-block; position: relative; left: 1em;
	animation: rpTri2 1s linear infinite;
}
@keyframes rpTri2{
	100% {
		opacity: 0;
		transform: translate(-100%, 0);
	}
}
.bmiColor1{color: #0AA; font-weight: 800;}
.bmiColor2{color: #F00; font-weight: 800;}
button {
	display: inline-block;
	width: fit-content;
	border-radius: 7px !important;
	/* font-size: 1.5em; */
	text-align: center;
	/* border: 2px solid #000; */
	padding: 0 2em;
	background: linear-gradient(to right, #000 50%, #DDD 50%);
	background-size: 200% 100%;
	background-position: 100% 0;
	transition: background-position .5s linear, color .5s linear;
}
button:hover {
	background-position: 0 0;
	color: #FFF;
	cursor: pointer;
}
